function EachstoreMain(props) {
const searchTerm = props.category;
const setSearchTerm = props.category;
return (
  <>

<div className="templateContainer-main">
    <div className="templateContainer-pad5">
      <div className="searchInput_Container">

      </div>

      <div className="template_Container">
        
        {
          data 
            .filter((val) => {
              if(searchTerm == ""){
                return val;
              } else if(val.category.toLowerCase().includes(searchTerm.toLowerCase())) {
                return val;
              }
            })
            .map((val) => {
              return(

                <div className="row py-3">
                <div className="row-sm-12 col-lg-3 key={val.id}">
                <Card3 imgsrcc1={val.image1} imgsrcc2={val.image2} price={val.price} title= {val.title} store={val.store} category={val.category} openhour={val.opens}/>
              </div> 
              </div>

              )
            })
        }
      </div>
   
      </div>
      </div>

  </>
)
}
export default EachstoreMain;