import React from 'react';
import './App1.css';
import Storeone from './comps/Storeone';
import Serviceone from './comps/Serviceone'
import Home from './comps/Home';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
        <Route exact path='/'><Home /></Route>
        <Route exact path='/storeone'><Storeone /></Route>
        <Route exact path='/serviceone'><Serviceone /></Route>
        </Switch>
        </div>
    </Router>
     
    
  );
}

export default App;
